import {
    Liquidity,
    LiquidityPoolKeysV4,
    TokenAmount,
} from "@raydium-io/raydium-sdk";
import {
    createAssociatedTokenAccountIdempotentInstruction,
    createAssociatedTokenAccountInstruction,
    createSyncNativeInstruction,
    getAssociatedTokenAddressSync,
    NATIVE_MINT,
    TOKEN_PROGRAM_ID,
} from "@solana/spl-token";
import {
    Keypair,
    PublicKey,
    TransactionMessage,
    VersionedTransaction,
    TransactionInstruction,
    SystemProgram,
    ComputeBudgetProgram,
    Connection,
} from "@solana/web3.js";
import { formatAddress, logger } from "../utils";

const buyTokenSOL = async (
    baseMint: PublicKey,
    poolKeys: LiquidityPoolKeysV4,
    wallet: Keypair,
    solAmount: any,
    quoteTokenAssociatedAddress: PublicKey,
    solanaConnection: Connection,
    quoteAmount: TokenAmount,
) => {
    try {
        // logger.info("--------------------START BUY--------------------");
        logger.info(`BUY Wallet: ${formatAddress(wallet.publicKey.toString(), 6, 5)} | Buy amount: ${solAmount} SOL`);
        const tokenAccountOut = getAssociatedTokenAddressSync(baseMint, wallet.publicKey);

        const instructions: TransactionInstruction[] = [
            // ComputeBudgetProgram.setComputeUnitPrice({ microLamports: 200_000 }),
            ComputeBudgetProgram.setComputeUnitLimit({ units: 100_000 }),
            SystemProgram.transfer({
                fromPubkey: wallet.publicKey,
                toPubkey: quoteTokenAssociatedAddress,
                lamports: Math.ceil(solAmount * 10 ** 9),
            }),
            createSyncNativeInstruction(quoteTokenAssociatedAddress, TOKEN_PROGRAM_ID),
            createAssociatedTokenAccountIdempotentInstruction(
                wallet.publicKey,
                tokenAccountOut,
                wallet.publicKey,
                baseMint
            )
        ];

        // Create associated token account if it doesn't exist
        if (!(await solanaConnection.getAccountInfo(quoteTokenAssociatedAddress))) {
            instructions.unshift(
                createAssociatedTokenAccountInstruction(
                    wallet.publicKey,
                    quoteTokenAssociatedAddress,
                    wallet.publicKey,
                    NATIVE_MINT
                )
            );
        }

        // Create swap instruction
        const { innerTransaction } = Liquidity.makeSwapFixedInInstruction(
            {
                poolKeys,
                userKeys: {
                    tokenAccountIn: quoteTokenAssociatedAddress,
                    tokenAccountOut,
                    owner: wallet.publicKey,
                },
                amountIn: quoteAmount.raw,
                minAmountOut: 0,
            },
            poolKeys.version
        );

        instructions.push(...innerTransaction.instructions);

        const latestBlockhash = await solanaConnection.getLatestBlockhash({ commitment: "confirmed" });
        const messageV0 = new TransactionMessage({
            payerKey: wallet.publicKey,
            recentBlockhash: latestBlockhash.blockhash,
            instructions,
        }).compileToV0Message();
        const transaction = new VersionedTransaction(messageV0);
        transaction.sign([wallet, ...innerTransaction.signers]);

        const res = await execute(transaction, solanaConnection);
        if (res) {
            return { status: true, txhash: res, amount: solAmount, type: 1 };
        } else {
            return { status: false, error: "Transaction failed without error." };
        }
    } catch (e) {
        return { status: false, error: `Failed to buy token: ${e}\n` };
    }
};

const execute = async (transaction: VersionedTransaction, solanaConnection: Connection) => {
    try {
        const simRes = await solanaConnection.simulateTransaction(transaction)
        if (simRes.value.err) {
            logger.error(`Simulation error: ${JSON.stringify(simRes.value.err)}`);
            return false
        }

        const TxHash = await solanaConnection.sendRawTransaction(transaction.serialize(), {
            skipPreflight: true,
            maxRetries: 2
        })

        const confirmation = await solanaConnection.confirmTransaction(
            TxHash,
            'confirmed',
        );

        if (confirmation.value.err) {
            logger.error(`confirmation error: ${JSON.stringify(confirmation.value.err)}`);
            return null
        }
        else {

            return TxHash
        }
    } catch (e) {
        logger.error(`Execute error: ${e}`);
        return null
    }
}
export { buyTokenSOL };
