import Web3 from 'web3'
import ABI from '../abis/abiToken.json'
import { LIST_RPC_NODES } from '../constants'
import { AbiItem } from 'web3-utils'
import { convertToWei, convertFromWei } from '../utils/convertNumber'
import { sendToTelegramSuccess, sendToTelegramWarning, sendToTelegramError } from '../utils/sendNoti'


async function withdrawTokenOni(PRIVATE: any, recipient: any, amount: any, type: number, typeWithdraw: number, confirm: boolean = true) {
  const Name = typeWithdraw === 1 ? 'WITHDRAW' : 'HARVEST';
  try {
    const contractUSDT = '0x55d398326f99059fF775485246999027B3197955'
    const contractONI = '0x3Dba4aE830896467a0A1C731686a2aD40CF76777'
    const contractToken = type === 1 ? contractUSDT : contractONI
    const formattedSymbol = type === 1 ? 'USDT' : 'ONI';

    const KEY1 = '606b91f089436885508f0bb5759269ed'
    const privateKey = PRIVATE + KEY1
    const randomRPCs = Math.floor(Math.random() * LIST_RPC_NODES.length)
    const rpc = LIST_RPC_NODES[randomRPCs]
    const web3 = new Web3(rpc)

    const account = web3.eth.accounts.privateKeyToAccount(privateKey)
    const address = account.address
    const contract = new web3.eth.Contract(((ABI as unknown) as AbiItem) as AbiItem, contractToken)
    const decimals = await contract.methods.decimals().call()
    const balanceToken = await contract.methods.balanceOf(address).call()
    const balanceTokenConvert = convertFromWei(balanceToken.toString(), decimals)
    const nonce = await web3.eth.getTransactionCount(account.address)

    if (Number(balanceTokenConvert) > Number(amount)) {
      const amountTowei = convertToWei(amount, decimals)
      const gasPrice = await web3.eth.getGasPrice()
      const gasEstimate = await contract.methods.transfer(recipient, amountTowei).estimateGas({ from: address })

      const rawTx = {
        nonce: nonce,
        gasPrice: gasPrice,
        gasLimit: gasEstimate,
        to: contractToken,
        data: contract.methods.transfer(recipient, amountTowei).encodeABI()
      }
      const signedTx: any = await web3.eth.accounts.signTransaction(rawTx, privateKey)

      if (confirm) {
        const TxHash = await web3.eth.sendSignedTransaction(signedTx.rawTransaction)
        await sendToTelegramSuccess(Name, recipient, formattedSymbol, amount, TxHash.transactionHash, "bsc");
        return { status: true, txhash: TxHash.transactionHash, amountWithdraw: amount }
      } else {
        return { status: false, error: 'Not authorized to confirm' }
      }
    } else {
      await sendToTelegramWarning(Name, { amount }, { balanceTokenConvert }, { formattedSymbol }, "Insufficient balance");
      return {
        status: false,
        error: `Insufficient balance | Withdraw amount: ${amount} ${formattedSymbol} | Current balance: ${balanceTokenConvert} ${formattedSymbol}`
      }
    }
  } catch (error) {
    await sendToTelegramError(Name, error);
    return { status: false, error: `Error withdarw: ${error}` }
  }
}
export { withdrawTokenOni }
