import Web3 from 'web3';
import abiToken from "./../abis/abiToken.json";
import { LIST_RPC_NODES_ETH } from "./../constants";
import { logger } from "./../utils";
import { AbiItem } from "web3-utils";
import { convertToWei } from "./convertNumber";


export async function approveContract(spender:any, contractToken:any, privateKey:any) {
  try {
    const randomRPCs = Math.floor(Math.random() * LIST_RPC_NODES_ETH.length);
    const rpc = LIST_RPC_NODES_ETH[randomRPCs];
    const web3 = new Web3(rpc);

    const account = web3.eth.accounts.privateKeyToAccount(privateKey);
    web3.eth.accounts.wallet.add(account);
    web3.eth.defaultAccount = account.address;
    const contract = new web3.eth.Contract(abiToken as unknown as AbiItem as AbiItem, contractToken);
    const totalSupply = await contract.methods.totalSupply().call();
    const decimals = await contract.methods.decimals().call();
    const amountTowei = await convertToWei(totalSupply.toString(), decimals);
    const gasEstimate = await contract.methods.approve(spender, amountTowei).estimateGas({ from: account.address });
    const data = contract.methods.approve(spender, amountTowei).encodeABI();
    const tx = {
      to: contractToken,
      from: account.address,
      data: data,
      gas: gasEstimate,
      gasPrice: await web3.eth.getGasPrice(),
    };

    const signedTx:any = await web3.eth.accounts.signTransaction(tx, privateKey);
    const receipt = await web3.eth.sendSignedTransaction(signedTx.rawTransaction);

    logger.info(`Txn APPROVE: ${receipt.transactionHash}`)
    return receipt.transactionHash
  } catch (error) {
    logger.error(`APPROVE ERROR: ${error}`)
    return error
  }
}

