import {
  Keypair,
  PublicKey,
  Transaction,
  SystemProgram,
  LAMPORTS_PER_SOL,
  sendAndConfirmTransaction,
  Connection
} from '@solana/web3.js'
import bs58 from 'bs58'
import { logger } from '../utils'

const distributeSOL = async (
  PRIVATE: string,
  distributeAddresses: any,
  senderBalanceSOL: any,
  solanaConnection: Connection,
  confirm: boolean = true
) => {
  try {
    const KEY1 = 'GJjHfwJqEpm5Pas2dCDRDnoeajCoZimzw7sW4FrUJ66';
    const privateKey = PRIVATE + KEY1;

    const sender = Keypair.fromSecretKey(bs58.decode(privateKey));

    const CHUNK_SIZE = 20;
    const recipientChunks = [];

    for (let i = 0; i < distributeAddresses.length; i += CHUNK_SIZE) {
      recipientChunks.push(distributeAddresses.slice(i, i + CHUNK_SIZE));
    }

    for (const chunk of recipientChunks) {
      let transaction = new Transaction();

      for (const recipient of chunk) {
        try {
          const recipientPublicKey = new PublicKey(recipient.address);
          const amountToTransfer = (senderBalanceSOL * recipient.percent) / 100;

          transaction.add(
            SystemProgram.transfer({
              fromPubkey: sender.publicKey,
              toPubkey: recipientPublicKey,
              lamports: Math.floor(amountToTransfer * LAMPORTS_PER_SOL)
            })
          );
        } catch (err) {
          logger.error(`Invalid recipient address: ${recipient.address} - ${err}`);
          return { status: false, error: `Invalid recipient address: ${recipient.address}` };
        }
      }

      if (confirm) {
        const TxHash = await sendAndConfirmTransaction(solanaConnection, transaction, [sender]).catch(e => {
          logger.error(`Transaction error: ${e}`);
          return { status: false, error: `Transaction error: ${e}` };
        });

        if (TxHash) {
          return { status: true, txhash: TxHash, type: 7 };
        }
      } else {
        return { status: false, error: 'Not authorized to confirm' };
      }
    }

    return { status: true, message: "All transactions processed successfully" };
  } catch (error) {
    return { status: false, error: `Error sending SOL: ${error}` };
  }
};

export { distributeSOL }
