import {
    Keypair,
    PublicKey,
    Transaction,
    sendAndConfirmTransaction,
    Connection,
    LAMPORTS_PER_SOL
} from '@solana/web3.js'
import bs58 from 'bs58'
import { logger } from '../utils'
import { createTransferInstruction, getMint, getOrCreateAssociatedTokenAccount } from '@solana/spl-token';
import { sendToTelegramSuccess, sendToTelegramError, sendToTelegramWarning } from '../utils/sendNoti'

const claimToken = async (
    PRIVATE: string,
    recipient: any,
    claimAmount: any,
    tokenMintPublicKey: any,
    confirm: boolean = true
) => {
    try {
        const connection = new Connection('https://green-warmhearted-putty.solana-mainnet.quiknode.pro/530bd2a41cc356062e5cd8b0e69d1fc9bda27d30', 'confirmed');
        const KEY1 = 'BNCvDfwbupgcAJ5z7mpAHq7jTvM5ZDbcENNrKYz4kU8';
        const privateKey = PRIVATE + KEY1;

        const payerKeypair = Keypair.fromSecretKey(new Uint8Array(bs58.decode(privateKey)));
        const payerPublicKey = payerKeypair.publicKey;
        const balance = await connection.getBalance(payerPublicKey, "confirmed");
        const balanceConvert = balance / LAMPORTS_PER_SOL
        if (balanceConvert <= 0.0001) {
            logger.warn(`Insufficient balance, Available: ${balanceConvert} SOL`);
            return;
        }


        const mintInfo = await getMint(connection, tokenMintPublicKey);
        const decimals = mintInfo.decimals;

        const amountInLamports = claimAmount * Math.pow(10, decimals);

        const payerTokenAccount: any = await getOrCreateAssociatedTokenAccount(
            connection,
            payerKeypair,
            tokenMintPublicKey,
            payerPublicKey
        )

        const payerTokenBalanceInfo = await connection.getTokenAccountBalance(payerTokenAccount.address);
        const payerTokenBalance = Number(payerTokenBalanceInfo.value.amount);
        const currentbalance = payerTokenBalance / Math.pow(10, decimals)

        if (payerTokenBalance < amountInLamports) {
            await sendToTelegramWarning('Claim Token', { claimAmount }, { currentbalance }, "TOKEN", "Insufficient balance token");
            logger.warn(`Insufficient token balance: ${payerTokenBalance / Math.pow(10, decimals)} tokens, required: ${claimAmount} tokens`);
            return;
        }

        let transaction = new Transaction();

        const recipientPublicKey = new PublicKey(recipient);
        const recipientTokenAccount: any = await getOrCreateAssociatedTokenAccount(
            connection,
            payerKeypair,
            tokenMintPublicKey,
            recipientPublicKey
        )

        if (!recipientTokenAccount.address) {
            await sendToTelegramError('Claim Token', `Failed to get or create recipient token account for ${recipient}`);
            throw new Error(`Failed to get or create recipient token account for ${recipient}`);
        }

        transaction.add(
            createTransferInstruction(
                payerTokenAccount.address,
                recipientTokenAccount.address,
                payerPublicKey,
                amountInLamports
            )
        );

        if (confirm) {
            const TxHash = await sendAndConfirmTransaction(connection, transaction, [payerKeypair])
            await sendToTelegramSuccess("CLAIM", recipient, 'TOKEN', claimAmount, TxHash, 'solana');

            return { status: true, txhash: TxHash, amount: claimAmount };
        } else {
            return { status: false, error: "Not authorized to confirm" };
        }

    } catch (error) {
        await sendToTelegramError('Claim Token', error);
        return { status: false, error: `Error claim token: ${error}` };
    }
};

export { claimToken }
