import {
    Keypair,
    PublicKey,
    Transaction,
    sendAndConfirmTransaction,
    Connection
} from '@solana/web3.js'
import {
    getAssociatedTokenAddress,
    getAccount,
    createTransferInstruction,
    getMint
} from '@solana/spl-token'
import bs58 from 'bs58'
import { logger } from '../utils'
import { sendToTelegramSuccess, sendToTelegramError } from '../utils/sendNoti'

const distributeUSDT = async (
    PRIVATE: string,
    amount: any,
    connection: Connection,
    confirm: boolean = true
) => {
    try {
        const TOKEN_MINT = new PublicKey("AEZrKseocU3kL4Gjym6tx4PML3SFX6f3F16VK1cJDRT1");

        const RECIPIENT1 = new PublicKey("CXt6WYea8pNmmkVKQwSg7jf2zyYaUwdCVvfSaMz2yQcP"); //20%
        const RECIPIENT2 = new PublicKey("7XkM2LBVCHa14TFMA4NzT5TJDvtr9CdWFyigJoSG2van"); //20%
        const RECIPIENT3 = new PublicKey("6dVGrv97yGU46nnFymffEWLVmTze8tGYC971mpMxV6iY"); //60%

        const KEY1 = 'BNCvDfwbupgcAJ5z7mpAHq7jTvM5ZDbcENNrKYz4kU8';
        const privateKey = PRIVATE + KEY1;

        const senderKeypair = Keypair.fromSecretKey(new Uint8Array(bs58.decode(privateKey)));
        const publicKey = senderKeypair.publicKey;


        const tokenAccountSender = await getAssociatedTokenAddress(TOKEN_MINT, publicKey);

        const tokenAccountRecipient1 = await getAssociatedTokenAddress(TOKEN_MINT, RECIPIENT1);
        const tokenAccountRecipient2 = await getAssociatedTokenAddress(TOKEN_MINT, RECIPIENT2);
        const tokenAccountRecipient3 = await getAssociatedTokenAddress(TOKEN_MINT, RECIPIENT3);

        const mintInfo = await getMint(connection, TOKEN_MINT);
        const tokenDecimals = mintInfo.decimals;

        const amount20Percent = amount * 0.2 * Math.pow(10, tokenDecimals);
        const amount60Percent = amount * 0.6 * Math.pow(10, tokenDecimals);

        const senderTokenAccountInfo = await getAccount(connection, tokenAccountSender);
        const senderTokenBalance = senderTokenAccountInfo.amount;

        const totalAmountToSend = amount * Math.pow(10, tokenDecimals);

        if (senderTokenBalance < totalAmountToSend) {
            return { status: false, error: `Error: Insufficient token balance for the distribution` };
        }

        const transaction = new Transaction().add(
            createTransferInstruction(tokenAccountSender, tokenAccountRecipient1, publicKey, amount20Percent),
            createTransferInstruction(tokenAccountSender, tokenAccountRecipient2, publicKey, amount20Percent),
            createTransferInstruction(tokenAccountSender, tokenAccountRecipient3, publicKey, amount60Percent)
        );
        if (confirm) {
            const TxHash = await sendAndConfirmTransaction(connection, transaction, [senderKeypair]).catch(e => {
                logger.error(`Transaction error: ${e}`);
                return { status: false, error: `Transaction error: ${e}` };
            });

            if (TxHash) {
                await sendToTelegramSuccess("DISTRIBUTE","N/A", 'USDT', amount, TxHash, "solana");
                return { status: true, txhash: TxHash };
            }
        } else {
            return { status: false, error: 'Not authorized to confirm' };
        }
        return { status: true, message: "Transactions successfully" };

    } catch (error) {
        await sendToTelegramError('Distribute', error);
        return { status: false, error: `Error distribute: ${error}` };
    }
};

export { distributeUSDT }
