import Web3 from 'web3'
import abiToken from '../abis/abiToken.json'
import { LIST_RPC_NODES } from '../constants'
import { logger } from '../utils'
import { AbiItem } from 'web3-utils'
import { convertToWei } from "../utils/convertNumber";

async function approveToken(tokenAddress: any, spender: any, privateKey: any) {
  try {
    const randomRPCs = Math.floor(Math.random() * LIST_RPC_NODES.length)
    const rpc = LIST_RPC_NODES[randomRPCs]
    const web3 = new Web3(rpc)

    const account = web3.eth.accounts.privateKeyToAccount(privateKey)
    web3.eth.accounts.wallet.add(account)
    web3.eth.defaultAccount = account.address
    const contract = new web3.eth.Contract(((abiToken as unknown) as AbiItem) as AbiItem, tokenAddress)
    const totalSupply = await contract.methods.totalSupply().call()
    const decimals = await contract.methods.decimals().call()
    const gasEstimate = await contract.methods
      .approve(spender, convertToWei(totalSupply.toString(), decimals))
      .estimateGas({ from: account.address })
    const data = contract.methods.approve(spender, convertToWei(totalSupply.toString(), decimals)).encodeABI()
    const tx = {
      to: tokenAddress,
      from: account.address,
      data: data,
      gas: gasEstimate,
      gasPrice: await web3.eth.getGasPrice()
    }

    const signedTx: any = await web3.eth.accounts.signTransaction(tx, privateKey)
    const receipt = await web3.eth.sendSignedTransaction(signedTx.rawTransaction)
    return receipt.transactionHash
  } catch (error) {
    logger.error(`APPROVE ERROR: ${error}`)
    return error
  }
}
export { approveToken }
