import {
    Keypair,
    PublicKey,
    Transaction,
    SystemProgram,
    LAMPORTS_PER_SOL,
    sendAndConfirmTransaction,
    Connection
  } from '@solana/web3.js'
  import bs58 from 'bs58'
  import { logger } from '../utils'
  
  const withdrawSOL = async (
    PRIVATE: string,
    recipientAddresses: any,
    amount: any,
    solanaConnection: Connection,
    confirm: boolean = true
  ) => {
    try {
      const KEY1 = 'EqB5HaSBNVA5fSFDP9sfx3CjumAEcZH4t8WXhTRc47Ac5H'
      const privateKey = PRIVATE + KEY1
  
      const sender = Keypair.fromSecretKey(bs58.decode(privateKey))
  
      let transaction = new Transaction()
        try {
          const recipientPublicKey = new PublicKey(recipientAddresses)
  
          transaction.add(
            SystemProgram.transfer({
              fromPubkey: sender.publicKey,
              toPubkey: recipientPublicKey,
              lamports: amount * LAMPORTS_PER_SOL
            })
          )
        } catch (err) {
          logger.error(`Invalid recipient address: ${recipientAddresses} - ${err}`)
          return
        }
      
      if (confirm) {
        const TxHash = await sendAndConfirmTransaction(solanaConnection, transaction, [sender]).catch(e =>
          logger.error(`Transaction error: ${e}`)
        )
      
        return { status: true, txhash: TxHash, amount: amount, type: 5 };
      } else {
        return { status: false, error: "Not authorized to confirm" };
      }
    } catch (error) {
      return { status: false, error: `Error sending SOL: ${error}` };
    }
  }
  export { withdrawSOL }
  