
import Web3 from 'web3';
import {approveContract} from "../utils/approve";
import abiAMM from "../abis/abiAMM.json";
import abiToken from "../abis/abiToken.json";
import { LIST_RPC_NODES,AMM_CONTRACT } from "../constants";
import { convertToWei,convertFromWei } from "../utils/convertNumber";
import { logger,sleep } from "../utils";
import { AbiItem } from "web3-utils";
import { sendToTelegramSuccess_EVM } from '../utils/sendNoti';

 async function sellTokenBSC(
  privateKey:any,
  amount:any,
  tokenAddress:any
) {
  const randomRPCs = Math.floor(Math.random() * LIST_RPC_NODES.length);
  const rpc = LIST_RPC_NODES[randomRPCs];
  const web3 = new Web3(rpc);

  const contractAMM = new web3.eth.Contract(abiAMM as unknown as AbiItem as AbiItem, AMM_CONTRACT);
  const account = web3.eth.accounts.privateKeyToAccount(privateKey);
  const walletAddress = account.address;

  const tokenContract = await new web3.eth.Contract(abiToken as unknown as AbiItem as AbiItem, tokenAddress);
  const decimals = await tokenContract.methods.decimals().call();
  const symbol = await tokenContract.methods.symbol().call();

  const allowance = await tokenContract.methods
    .allowance(account.address, AMM_CONTRACT)
    .call();
  const allowanceFromWei = await convertFromWei(allowance, decimals);
  if (Number(allowanceFromWei) < Number(amount)) {
    await approveContract(AMM_CONTRACT, tokenAddress, privateKey,);
    await sleep(7);
  }
  // done check

  const amountInWei = convertToWei(amount.toString(), decimals);
  const nonce = await web3.eth.getTransactionCount(account.address);
  const gasPrice = await web3.eth.getGasPrice();
  const gasEstimate = await contractAMM.methods
  .swapExactTokensForETHOnlySell(tokenAddress, amountInWei)
  .estimateGas({
    from: walletAddress
  });
  const transaction = {
    nonce: nonce,
    from: walletAddress,
    to: AMM_CONTRACT,
    gas: gasEstimate,
    gasPrice: gasPrice,
    data: contractAMM.methods.swapExactTokensForETHOnlySell(tokenAddress, amountInWei).encodeABI(),
  };
 
  const signedTransaction:any = await web3.eth.accounts.signTransaction(
    transaction,
    privateKey
  );
  const receipt = await web3.eth.sendSignedTransaction(
    signedTransaction.rawTransaction
  );
  await sendToTelegramSuccess_EVM(`SELL ${symbol}`,tokenAddress, symbol, `${amount} ${symbol}`,privateKey, receipt.transactionHash, 'bsc')
  logger.info(`Txn SELL: ${receipt.transactionHash}`)
  return receipt.transactionHash
}
export { sellTokenBSC };
