import {
    Keypair,
    PublicKey,
    Transaction,
    sendAndConfirmTransaction,
    Connection,
    LAMPORTS_PER_SOL
} from '@solana/web3.js'
import bs58 from 'bs58'
import { logger } from '../utils'
import { createTransferInstruction, getMint, getOrCreateAssociatedTokenAccount } from '@solana/spl-token';
import { sendToTelegramSuccess, sendToTelegramError,sendToTelegramWarning } from '../utils/sendNoti'

const connection = new Connection('https://green-warmhearted-putty.solana-mainnet.quiknode.pro/530bd2a41cc356062e5cd8b0e69d1fc9bda27d30');
const TOKEN_MINT = new PublicKey('AEZrKseocU3kL4Gjym6tx4PML3SFX6f3F16VK1cJDRT1');
const recipientWallet = 'CXt6WYea8pNmmkVKQwSg7jf2zyYaUwdCVvfSaMz2yQcP'; // v tng nhn USDT

const decodeTransaction = async (txHash: any) => {
    try {
        const txInfo = await connection.getParsedTransaction(txHash, 'confirmed');
        if (!txInfo) {
            logger.error('Transaction not found!');
            return null;
        }

        const mintInfo = await getMint(connection, TOKEN_MINT);
        const tokenDecimals = mintInfo.decimals;
        let sender = null, recipient = null, tokenAmount = null, solAmount = null;

        txInfo.transaction.message.instructions.forEach((instruction: any) => {
            const parsed = instruction.parsed;

            if (parsed && parsed.type === 'transfer') {
                const info = parsed.info;
                if (info.lamports) {
                    solAmount = info.lamports / LAMPORTS_PER_SOL;
                    sender = info.source;
                    recipient = info.destination;
                } else if (info.amount) {
                    tokenAmount = info.amount / Math.pow(10, tokenDecimals);
                    sender = info.source;
                    recipient = info.destination;
                }
            }
        });

        return { status: true, sender, recipient, tokenAmount, solAmount };
    } catch (error) {
        return { status: false, error: `Error decoding transaction: ${error}` };
    }
};

const sellTokenOTC = async (
    PRIVATE: string,
    recipientOTC: string,
    amountBuy: any,
    priceToken: any,
    txHash: string,
    confirm: boolean = true
) => {
    try {
        const resHash: any = await decodeTransaction(txHash);

        if (!resHash) {
            logger.warn('Decode transaction failed or transaction not found.');
            return;
        }

        if (resHash.status &&
            resHash.sender === recipientOTC &&
            resHash.recipient === recipientWallet &&
            resHash.tokenAmount === amountBuy) {

            const KEY1 = 'BNCvDfwbupgcAJ5z7mpAHq7jTvM5ZDbcENNrKYz4kU8';
            const privateKey = PRIVATE + KEY1;
            const payerKeypair = Keypair.fromSecretKey(new Uint8Array(bs58.decode(privateKey)));
            const payerPublicKey = payerKeypair.publicKey;

            const balance = await connection.getBalance(payerPublicKey, 'confirmed');
            const balanceConvert = balance / LAMPORTS_PER_SOL;

            if (balanceConvert <= 0.001) {
                await sendToTelegramWarning('SEND OTC',{balanceConvert},{balanceConvert}, "TOKEN","Insufficient balance SOL");
                logger.warn(`Insufficient balance: ${balanceConvert} SOL`);
                return;
            }

            const amountSender = Number(amountBuy) / Number(priceToken);
            const mintInfo = await getMint(connection, TOKEN_MINT);
            const decimals = mintInfo.decimals;
            let amountInLamports = amountSender * Math.pow(10, decimals);

            amountInLamports = Math.round(amountInLamports);
            const payerTokenAccount = await getOrCreateAssociatedTokenAccount(
                connection,
                payerKeypair,
                TOKEN_MINT,
                payerPublicKey
            ).catch(e => {
                logger.error('Error getting payer token account:', e);
                throw e;
            });

            const payerTokenBalanceInfo = await connection.getTokenAccountBalance(payerTokenAccount.address);
            const payerTokenBalance = Number(payerTokenBalanceInfo.value.amount);
            const currentbalance = payerTokenBalance / Math.pow(10, decimals)

            if (payerTokenBalance < amountInLamports) {
                await sendToTelegramWarning('SEND OTC',{amountSender},{currentbalance}, "TOKEN","Insufficient balance token");
                logger.warn(`Insufficient token balance: ${payerTokenBalance / Math.pow(10, decimals)} tokens, required: ${amountSender} tokens`);
                return;
            }

            const recipientPublicKey = new PublicKey(recipientOTC);
            const recipientTokenAccount: any = await getOrCreateAssociatedTokenAccount(
                connection,
                payerKeypair,
                TOKEN_MINT,
                recipientPublicKey
            )

            const transaction: any = new Transaction().add(
                createTransferInstruction(
                    payerTokenAccount.address,
                    recipientTokenAccount.address,
                    payerPublicKey,
                    amountInLamports
                )
            );

            if (confirm) {
                const TxHash = await sendAndConfirmTransaction(connection, transaction, [payerKeypair])

                await sendToTelegramSuccess("SEND OTC",recipientOTC, 'TOKEN', amountSender, TxHash, 'solana');
                return { status: true, txhash: TxHash, amount: amountSender };
            } else {
                return { status: false, error: "Not authorized to confirm" };
            }
        } else {
            await sendToTelegramError('sendTokenOTC', 'Transaction is invalid or does not match the requirements.');
            return { status: false, error: "Transaction is invalid or does not match the requirements." };
        }
    } catch (error) {
        await sendToTelegramError('sendTokenOTC', error);
        return { status: false, error: `Error in sendTokenOTC: ${error}` };
    }
};

export { sellTokenOTC }
