
import {
    createCloseAccountInstruction,
} from "@solana/spl-token";
import {
    Keypair,
    PublicKey,
    TransactionMessage,
    VersionedTransaction,
    Connection,
} from "@solana/web3.js";
import { logger } from "../utils";

const unwrapSOL = async (
    wallet: Keypair,
    solanaConnection: Connection,
    quoteMintAddress: PublicKey
) => {
    try {
        // logger.info("Unwrapping WSOL");
        const wsolAccountInfo = await solanaConnection.getAccountInfo(quoteMintAddress);
        if (!wsolAccountInfo) {
          
            return { status: false, error: "WSOL account not found, skipping unwrap." };
            
        }

        const wsolBalance = await solanaConnection.getBalance(quoteMintAddress);
        const wsolAmount = wsolBalance / 10 ** 9;
        if (wsolAmount > 0) {
            logger.info(`Trying to unwrap ${wsolAmount} wsol to sol`)
        } else {
            return { status: false, error: "WSOL balance is zero, nothing to unwrap" };
        }

        const instructions = []
        instructions.push(
            createCloseAccountInstruction(
                quoteMintAddress,
                wallet.publicKey,
                wallet.publicKey
            )
        )
        const latestBlockhash = await solanaConnection.getLatestBlockhash({
            commitment: 'confirmed',
        })

        const messageV0 = new TransactionMessage({
            payerKey: wallet.publicKey,
            recentBlockhash: latestBlockhash.blockhash,
            instructions: [...instructions],
        }).compileToV0Message()

        const transaction = new VersionedTransaction(messageV0)
        transaction.sign([wallet])
        const res = await execute(transaction, solanaConnection)
        if (res) {
            // logger.info(`TxHash unwrapped: ${res}`);
            // logger.info("Successfully unwrapped WSOL to SOL\n")
            return { status: true, txhash: res, amount: wsolAmount, type: 3 };
        } else {
            // logger.warn("Unwrapping WSOL failed\n");
            return { status: false, error: "Unwrapping WSOL failed." };
        }
    } catch (error) {
        logger.error(`Error unwrapping WSOL: ${error}`);
        return { status: false, error: `Error unwrapping WSOL: ${error}` };
    }
};
const execute = async (transaction: VersionedTransaction, solanaConnection: Connection) => {
    try {
        const simRes = await solanaConnection.simulateTransaction(transaction)
        if (simRes.value.err) {
            logger.error(`simRes error: ${simRes.value.err}`);
            return false
        }

        const TxHash = await solanaConnection.sendRawTransaction(transaction.serialize(), {
            skipPreflight: true,
            maxRetries: 2
        })

        const confirmation = await solanaConnection.confirmTransaction(
            TxHash,
            'confirmed',
        );

        if (confirmation.value.err) {
            logger.error(`confirmation error: ${confirmation.value.err}`);
            return null
        }
        else {

            return TxHash
        }
    } catch (e) {
        logger.error(`Execute error: ${e}`);
        return null
    }
}
export { unwrapSOL };
