import {
    Liquidity,
    LiquidityPoolKeys,
} from "@raydium-io/raydium-sdk";

import {
    Keypair,
    PublicKey,
    TransactionMessage,
    VersionedTransaction,
    ComputeBudgetProgram,
    Connection,
} from "@solana/web3.js";
import { formatAddress, logger } from "../utils";

const sellTokenSOL = async (
    poolId: PublicKey,
    ata: PublicKey,
    mint: PublicKey,
    amountSell: string,
    amountConvert: string,
    poolKeys: LiquidityPoolKeys,
    wallet: Keypair,
    quoteTokenAssociatedAddress: PublicKey,
    solanaConnection: Connection,
    sellAgain: boolean = false,
    retryCount: number = 0
) => {
    try {
        const action = sellAgain ? `SELL AGAIN (Attempt ${retryCount + 1})` : "START SELL";
        logger.info(`--------------------${action}--------------------`);
        logger.info(`SELL Wallet: ${formatAddress(wallet.publicKey.toString(), 6, 5)} | Sell amount: ${amountConvert}`);

        if (sellAgain) {
            const info = await solanaConnection.getTokenAccountBalance(ata);
            if (info.value.uiAmount === 0) {
                return { status: false, error: "No tokens left to sell." };
            }
            if (retryCount >= 15) {
                logger.warn("Maximum retry attempts reached. Aborting sell.");
                return { status: false, error: "Transaction failed without error." };
            }
        }

        const { innerTransaction } = Liquidity.makeSwapFixedInInstruction(
            {
                poolKeys,
                userKeys: {
                    tokenAccountOut: quoteTokenAssociatedAddress,
                    tokenAccountIn: ata,
                    owner: wallet.publicKey,
                },
                amountIn: amountSell,
                minAmountOut: 0,
            },
            poolKeys.version
        );

        const latestBlockhash = await solanaConnection.getLatestBlockhash({
            commitment: "confirmed",
        });

        const messageV0 = new TransactionMessage({
            payerKey: wallet.publicKey,
            recentBlockhash: latestBlockhash.blockhash,
            instructions: [
                // ComputeBudgetProgram.setComputeUnitPrice({ microLamports: 200_000 }),
                ComputeBudgetProgram.setComputeUnitLimit({ units: 100_000 }),
                ...innerTransaction.instructions,
            ],
        }).compileToV0Message();

        const transaction = new VersionedTransaction(messageV0);
        transaction.sign([wallet, ...innerTransaction.signers]);

        const res = await execute(transaction, solanaConnection);
        if (res) {
          
            return { status: true, txhash: res, amount: amountConvert, type: 2 };
        } else {

            logger.warn(`Sell transaction failed, retrying...\n`);
            await sellTokenSOL(poolId, ata, mint, amountSell, amountConvert, poolKeys, wallet, quoteTokenAssociatedAddress, solanaConnection, true, retryCount + 1);
            return { status: false, error: "Transaction failed without error." };
        }

    } catch (e) {
        return { status: false, error: `Failed to sell token: ${e}\n` };
    }
};
const execute = async (transaction: VersionedTransaction, solanaConnection: Connection) => {
    try {
        const simRes = await solanaConnection.simulateTransaction(transaction)
        if (simRes.value.err) {
            logger.error(`Simulation error: ${JSON.stringify(simRes.value.err)}`);
            return false
        }

        const TxHash = await solanaConnection.sendRawTransaction(transaction.serialize(), {
            skipPreflight: true,
            maxRetries: 2
        })

        const confirmation = await solanaConnection.confirmTransaction(
            TxHash,
            'confirmed',
        );

        if (confirmation.value.err) {
            logger.error(`confirmation error: ${JSON.stringify(confirmation.value.err)}`);
            return null
        }
        else {

            return TxHash
        }
    } catch (e) {
        logger.error(`Execute error: ${e}`);
        return null
    }
}
export { sellTokenSOL };
