import {
  Keypair,
  PublicKey,
  Transaction,
  SystemProgram,
  Connection,
  LAMPORTS_PER_SOL
} from '@solana/web3.js'
import bs58 from 'bs58'
import { RECIPIENT } from '../constants'

const collectionSOL = async (
  privateKey: string,
  solanaConnection: Connection,
  blockhash: any,
  lamportsToSend: any,
  confirm: boolean = true
) => {
  try {
    const sender = Keypair.fromSecretKey(bs58.decode(privateKey))

    const transaction = new Transaction().add(
      SystemProgram.transfer({
        fromPubkey: sender.publicKey,
        toPubkey: new PublicKey(RECIPIENT),
        lamports: lamportsToSend * LAMPORTS_PER_SOL
      })
    )

    transaction.recentBlockhash = blockhash
    transaction.feePayer = sender.publicKey

    if (confirm) {
      const TxHash = await solanaConnection.sendTransaction(transaction, [sender], {
        skipPreflight: false,
        preflightCommitment: 'confirmed'
      })
      const confirmation = await solanaConnection.confirmTransaction(TxHash, 'confirmed')

      if (confirmation.value.err) {
        return { status: false, error: `Transaction failed: ${JSON.stringify(confirmation.value.err)}`};

      } else {
        return { status: true, txhash: TxHash, amount: lamportsToSend, type: 4 };
      }
    } else {
      return { status: false, error: "Not authorized to confirm" };
    }
  } catch (error) {
    return { status: false, error: `Error sending SOL: ${error}`};
    
  }
}
export { collectionSOL }
