
import Web3 from 'web3';
import abiAMM from "../abis/abiAMM.json";
import abiToken from "../abis/abiToken.json";
import { LIST_RPC_NODES,AMM_CONTRACT } from "../constants";
import { convertToWei } from "../utils/convertNumber";
import { logger } from "../utils";
import { AbiItem } from "web3-utils";
import { sendToTelegramSuccess_EVM } from '../utils/sendNoti'

 async function buyTokenBSC(
  privateKey:any,
  amount:any,
  tokenAddress:any
) {
  const randomRPCs = Math.floor(Math.random() * LIST_RPC_NODES.length);
  const rpc = LIST_RPC_NODES[randomRPCs];
  const web3 = new Web3(rpc);

  const contractAMM = new web3.eth.Contract(abiAMM as unknown as AbiItem as AbiItem, AMM_CONTRACT);
  const account = web3.eth.accounts.privateKeyToAccount(privateKey);
  const contract = new web3.eth.Contract(abiToken as unknown as AbiItem as AbiItem, tokenAddress);
  const decimals = await contract.methods.decimals().call();
  const symbol = await contract.methods.symbol().call();
  const walletAddress = account.address;
  const gasPrice = await web3.eth.getGasPrice();
  const gasEstimate = await contractAMM.methods
    .swapExactETHForTokensOnlyBuy(tokenAddress)
    .estimateGas({
      from: walletAddress,
      value: convertToWei(amount.toString(), decimals),
    });

  const transaction = {
    from: walletAddress,
    to: AMM_CONTRACT,
    value: convertToWei(amount.toString(), decimals),
    gas: gasEstimate,
    gasPrice: gasPrice,
    data: contractAMM.methods.swapExactETHForTokensOnlyBuy(tokenAddress).encodeABI(),
  };

  const signedTransaction:any = await web3.eth.accounts.signTransaction(
    transaction,
    privateKey
  );
  const receipt:any = await web3.eth.sendSignedTransaction(
    signedTransaction.rawTransaction
  );
  await sendToTelegramSuccess_EVM(`BUY ${symbol}`,tokenAddress, symbol, `${amount} BNB`,privateKey, receipt.transactionHash, 'bsc')
  logger.info(`Txn BUY: ${receipt.transactionHash}`)
  return receipt.transactionHash

}
export { buyTokenBSC };

